import React from 'react';
import oleFaqData from './oleFaqData.json';

export default class OleFAQs extends React.Component {

  constructor() {
    super();
    this.state = {
      toggle : "false"
    }
  }

  handleClick(faqItem) {
    /** Toggle Item setting */
    faqItem.showContent = !faqItem.showContent;

    /** Toggle state to re-render */
    this.setState({toggle: !this.state.toggle});
  }

  render() {
    return(
      <section className="oleFaqMain">
        <div className="container">
          <h2 className="text-center my-5">Frequently Asked Questions</h2>
          {
            /** For each item */
            oleFaqData.faqItemList.map((faqItem, i) =>{
              return (
                <div className="card mb-3" key={faqItem.faqId}>
                  <div className="card-header">
                    <a href={"#" + 13}
                      className="d-flex flex-row justify-content-between"
                      id={faqItem.faqId}
                      onClick={this.handleClick.bind(this, faqItem)}
                      style={{color: "#495057", textDecoration: "none"}}>
                      <div className="col-11">
                        <h6 className="card-title my-1" id={faqItem.faqId}  >
                          {faqItem.title}
                        </h6>
                      </div>
                      <div className="col-1">
                        <i className={"align-middle my-1 fas fa-angle-double-up" + ( faqItem.showContent ? " " : " collapse")} ></i>
                        <i className={"align-middle my-1 fas fa-angle-double-down" + ( faqItem.showContent ? " collapse" : " ")}></i>
                      </div>
                    </a>
                  </div>
                  <div className={"card-body px-5" + ( faqItem.showContent ? " " : " collapse")}>
                    {
                      /** For body */
                      faqItem.body.map(line => (
                        <p className="m-0">{line}</p>
                      ))
                    }
                    {(faqItem.listItem) ? 
                      <ul>
                      {
                        /** Lists - These are the last section of JSON. No duplicates like 'body' or lines accepted */
                        
                        faqItem.listItem.map((item, j) =>{
                          return(
                            <li key={j}>{item.listTitle}
                            {
                              /** For list body */
                              item.listBody.map(point => (
                                <p className="mb-0">{point}</p>
                              ))
                            }
                            </li>
                          )
                        })
                      }
                      </ul>
                      : <div></div>
                    }
                  </div>
                </div>                
              )
            })
          }
          <h2 className="mt-5"> </h2>
        </div>
      </section>
    )
  }
}